.contractWrapper {
  height: 15vh;
  line-height: 15vh;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: larger;
  font-weight: bold;
  color: #ffffff;
  background-color: #5bc09a;
  border: 2px solid #222;
  border-radius: 4px;
  transition: background-color 0.75s, color 0.75s, border 0.75s, font-size 0.75s;
}

.contractWrapper:hover {
  background-color: #ffffff;
  font-size: x-large;
  color: #5bc09a;
  border: 2px solid rgb(255, 255, 255);
  cursor: pointer;
}
