.App {
  width: 100vw;
  min-height: 100vh;
  height: 110vh;
  text-align: center;
  background-image: url("./PaperBlack.jpg");
  background-size: cover;
}

.App > nav {
  text-align: left;
  line-height: 1.8;
  font-size: 16px;
}

.App > nav > img {
  width: 355px;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 100px;
}

.appButton {
  width: 100%;
  height: 5vh;
  line-height: 2.5vh;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: large;
  font-weight: bold;
  color: #ffffff;
  border: 1px solid #222;
  background-color: #5bc09a;
  border-radius: 4px;
  transition: background-color 0.5s, color 0.5s, border 0.5s, font-size 0.5s;
}

.appButton:hover {
  background-color: #ffffff;
  color: #5bc09a;
  font-size: larger;
  border: 2px solid rgb(255, 255, 255);
  cursor: pointer;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
