.functionName {
  border: 1px solid #ffffff;
  color: #ffffff;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-weight: bold;
  background: #1976d2;
  height: 5vh;
  line-height: 5vh;
}

.functionWrapper {
  display: grid;
  grid-template-rows: 3vh;
  grid-template-columns: calc(20vw / 3) calc(20vw / 3) calc(20vw / 3);
}
