.ownerWrapper {
  display: block;
  width: 100vw;
  height: calc(100vh - ((80vh - 30vw) / 2));
  text-align: center;
}

.ownerGrid {
  display: grid;
  width: 80vw;
  padding-left: 10vw;
  padding-top: 5vh;
  grid-template-areas:
    "tab1 tab2 tab3 tab4"
    "ownerDisplay ownerDisplay ownerDisplay ownerDisplay";
  grid-template-columns: 20vw 20vw 20vw 20vw;
}

.ownerTab {
  text-align: center;
  height: 10vh;
  line-height: 10vh;
  background: #5bc09a;
  transition: font-size 0.66s, color 0.66s, background-color 0.66s;
}

.ownerTab:hover {
  font-size: larger;
  color: #5bc09a;
  background-color: #ffffff;
}

.ownerTab:active {
  font-size: larger;
  color: #5bc09a;
  background-color: #ffffff;
}

.ownerDisplay {
  width: 80vw;
  height: 80vh;
}

.addressInput {
  margin-top: 10vh;
  width: 15vw;
}

.mintButton {
  background: #5bc09a;
  display: block;
  margin: auto;
  margin-top: 5vh;
  width: 15vw;
  height: 10vh;
  font-size: larger;
  font-weight: bolder;
  border-radius: 15px;
  transition: font-size 0.5s;
}

.mintButton:hover {
  background: #ffffff;
  font-size: x-large;
}

.nftImage {
  display: inline-block;
  width: 15vw;
  height: 15vw;
  margin: auto;
}

.tabImage {
  width: 5vw;
  height: 5vw;
  margin-top: 10vh;
  margin-left: 2.5vw;
}
