.mintWrapper {
  display: inline-block;
  width: 45vw;
  vertical-align: top;
  margin: 0;
  margin-top: 5vh;
}

.tmaPicture {
  margin: auto;
  display: block;
  width: 30vw;
  height: 30vw;
}

.mintButton {
  display: block;
  margin: auto;
  margin-top: 5vh;
  width: 15vw;
  height: 20vh;
  font-size: larger;
  font-weight: bolder;
  border-radius: 50px;
  transition: font-size 0.5s;
}

.mintButton:hover {
  font-size: x-large;
}

.nftImage {
  display: inline-block;
  width: 15vw;
  height: 15vw;
  margin: auto;
}
